import { createStore } from 'vuex'
import GlobalAxios from 'axios'
import axios from '../axios-auth'
import router from '../router/index'

// local storage variables
// userId
// token

export default createStore({
  debug: true,
  state: {
    errorLogin: null,
    auth_token: null,
    user_id: null,
    user: null,
    question_text: null,
    question_number: null,
    examSessionId: null,
    isAuthenticated: false,
    isLoading: false,
    error: null
  },
  mutations: {
    SET_AUTH_TOKEN (newValue) {
      this.state.auth_token = newValue
      localStorage.token = newValue
    },
    authUser (state, userData) {
      state.token = userData.token
      state.user_id = userData.user_id
      state.auth_token = userData.token
      state.isAuthenticated = userData.auth
    },
    updateExamSessionId (state, sessionId) {
      state.examSessionId = sessionId
    }
  },
  actions: {
    getAuthToken () {
      return this.state.auth_token
    },
    updateAuthToken ({ commit }, newValue) {
      console.log('attempting to update token with ' + newValue)
      commit('SET_AUTH_TOKEN', newValue)
    },
    setExamToCompleted ({ commit, dispatch }, sessionId) {
      axios.post('api/exam/complete_exam/', {
        session_id: sessionId
      }, {
        headers: { Authorization: `Token ${localStorage.token}` }
      }
      )
        .then(res => {
          console.log(res)
          console.log('session id store =>', sessionId)
          commit('updateExamSessionId', {
            examSessionId: sessionId
          })
          router.push({
            name: 'score'
          })
        })
    },
    signup ({ commit, dispatch }, authData) {
      axios.post('api/account/register', {
        username: authData.username,
        email: authData.email,
        password: authData.password,
        first_name: authData.first_name,
        last_name: authData.last_name
      })
        .then(res => {
          console.log(res)
          commit('authUser', {
            token: res.data.auth_token,
            user_id: res.data.id
          })
          dispatch('storeUser', authData)
        })
        .catch(err => console.log(err))
    },
    login ({ commit }, authData) {
      console.log('login pressed')

      console.log('username = ' + authData.username)
      console.log('password = ' + authData.password)
      this.state.errorLogin = false
      this.state.isLoading = true
      axios.post('/auth/token/login', {
        username: authData.username,
        password: authData.password

      })
        .then(res => {
          // this.state.isLoading = false
          console.log(res)
          this.auth_token = res.data.auth_token
          localStorage.token = this.auth_token
          localStorage.userId = res.data.id
          localStorage.userName = res.data.first_name
          commit('authUser', {
            token: res.data.auth_token,
            user_id: res.data.id,
            auth: true
          })
          this.isLoading = false
          router.replace({
            name: 'UserHomePage'
          })
        })
        .catch(error => {
          this.state.isLoading = false
          this.state.errorLogin = true
          console.log('in error loop')
          console.log(error)
        })
    },
    loginToBillingError ({ commit }, authData) {
      console.log('login to billing pressed')

      console.log('username = ' + authData.username)
      console.log('password = ' + authData.password)

      axios.post('/auth/token/login', {
        username: authData.username,
        password: authData.password

      })
        .then(res => {
          this.isLoading = true
          console.log(res)
          this.auth_token = res.data.auth_token
          localStorage.token = this.auth_token
          localStorage.userId = res.data.id
          localStorage.userName = res.data.first_name
          commit('authUser', {
            token: res.data.auth_token,
            user_id: res.data.id,
            auth: true
          })
          this.isLoading = false
          router.replace({
            name: 'InitialBillingError'
          })
        })
        .catch(error => {
          console.log('in error loop')
          console.log(error)
        })
    },
    checkForPreviousExam () {
      axios.get('api/exam/continue-check/', {
        headers: { Authorization: 'Token ' + localStorage.token },
        params: { user_id: localStorage.userId }
      })
        .then(res => {
          console.log(res)
        }).catch(err => { console.log(err) })
    },
    storeUser ({ commit }, userData) {
      if (!this.state.auth_token) {
        return
      }
      GlobalAxios.post('api/account/test/', userData, { headers: { Authorization: `Token ${localStorage.token}` } })
        .then(res => {
          console.log(res)
        })
        .catch(err => { console.log(err) })
    }
  },
  methods: {
    fetchUser () {
      if (!this.state.auth_token) {
        return
      }
      GlobalAxios.post('api/account/user/', {
        headers: { Authorization: `Token ${localStorage.token}` }
      })
        .then(res => {
          console.log(res)
        })
        .catch(err => { console.log(err) })
    }
  },
  modules: {
  }
})
